import VueRouter from 'vue-router'

//引入组件
import Index from '../pages'
import Home from '../pages/home/home'
import Login from '../pages/login/login'
import Register from '../pages/register/register'
import Center from '../pages/center/center'
import Storeup from '../pages/storeup/list'
import AddrList from '../pages/shop-address/list'
import AddrAdd from '../pages/shop-address/addOrUpdate'
import Order from '../pages/shop-order/list'
import OrderConfirm from '../pages/shop-order/confirm'
import Cart from '../pages/shop-cart/list'
import News from '../pages/news/news-list'
import NewsDetail from '../pages/news/news-detail'
import yonghuList from '../pages/yonghu/list'
import yonghuDetail from '../pages/yonghu/detail'
import yonghuAdd from '../pages/yonghu/add'
import shangjiaList from '../pages/shangjia/list'
import shangjiaDetail from '../pages/shangjia/detail'
import shangjiaAdd from '../pages/shangjia/add'
import piaowuxinxiList from '../pages/piaowuxinxi/list'
import piaowuxinxiDetail from '../pages/piaowuxinxi/detail'
import piaowuxinxiAdd from '../pages/piaowuxinxi/add'
import jiudianxinxiList from '../pages/jiudianxinxi/list'
import jiudianxinxiDetail from '../pages/jiudianxinxi/detail'
import jiudianxinxiAdd from '../pages/jiudianxinxi/add'
import taocanchanpinList from '../pages/taocanchanpin/list'
import taocanchanpinDetail from '../pages/taocanchanpin/detail'
import taocanchanpinAdd from '../pages/taocanchanpin/add'
import shangpinhushouList from '../pages/shangpinhushou/list'
import shangpinhushouDetail from '../pages/shangpinhushou/detail'
import shangpinhushouAdd from '../pages/shangpinhushou/add'

const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
	return originalPush.call(this, location).catch(err => err)
}

//配置路由
export default new VueRouter({
	routes:[
		{
      path: '/',
      redirect: '/index/home'
    },
		{
			path: '/index',
			component: Index,
			children:[
				{
					path: 'home',
					component: Home
				},
				{
					path: 'center',
					component: Center,
				},
				{
					path: 'storeup',
					component: Storeup
				},
                {
                    path: 'shop-address/list',
                    component: AddrList
                },
                {
                    path: 'shop-address/addOrUpdate',
                    component: AddrAdd
                },
				{
					path: 'shop-order/order',
					component: Order
				},
				{
					path: 'cart',
					component: Cart
				},
				{
					path: 'shop-order/orderConfirm',
					component: OrderConfirm
				},
				{
					path: 'news',
					component: News
				},
				{
					path: 'newsDetail',
					component: NewsDetail
				},
				{
					path: 'yonghu',
					component: yonghuList
				},
				{
					path: 'yonghuDetail',
					component: yonghuDetail
				},
				{
					path: 'yonghuAdd',
					component: yonghuAdd
				},
				{
					path: 'shangjia',
					component: shangjiaList
				},
				{
					path: 'shangjiaDetail',
					component: shangjiaDetail
				},
				{
					path: 'shangjiaAdd',
					component: shangjiaAdd
				},
				{
					path: 'piaowuxinxi',
					component: piaowuxinxiList
				},
				{
					path: 'piaowuxinxiDetail',
					component: piaowuxinxiDetail
				},
				{
					path: 'piaowuxinxiAdd',
					component: piaowuxinxiAdd
				},
				{
					path: 'jiudianxinxi',
					component: jiudianxinxiList
				},
				{
					path: 'jiudianxinxiDetail',
					component: jiudianxinxiDetail
				},
				{
					path: 'jiudianxinxiAdd',
					component: jiudianxinxiAdd
				},
				{
					path: 'taocanchanpin',
					component: taocanchanpinList
				},
				{
					path: 'taocanchanpinDetail',
					component: taocanchanpinDetail
				},
				{
					path: 'taocanchanpinAdd',
					component: taocanchanpinAdd
				},
				{
					path: 'shangpinhushou',
					component: shangpinhushouList
				},
				{
					path: 'shangpinhushouDetail',
					component: shangpinhushouDetail
				},
				{
					path: 'shangpinhushouAdd',
					component: shangpinhushouAdd
				},
			]
		},
		{
			path: '/login',
			component: Login
		},
		{
			path: '/register',
			component: Register
		},
	]
})
